



































































































































































































































	import server from '../../../store/server.js';
	export default {
		data() {
			return {
				pageId: '',
				Token: '',
				SubcontractingUnit: '',
				SubcontractingUnitName: '',
				Title: '',
				Number: '',
				Project: '',
				RectPeople: '',
				Company: '',
				CompanyName:'',
				Building: '',
				Floor: '',
				SafetyDanger: '',
				DangerDes: '',
				ExpirDate: '',
				CrashLevel: '',
				CheckDate: '',
				ReCheckDate: '',
				State: '',
				Rectification: '',
				AddPeople: '',
				isReply: false, //回复
				isApproval: false, //审批通过
				isRejected: false, //审批拒绝
				isRecheck: false, //复查
				isFinished: false, //完成
				replyContent: '', //回复内容
				rejuectContent: '', //拒绝理由
				imageList:[],
				repleyList:[],
				ReState:0,
				RefuseReason:''
			}
		},
		onLoad(option) {
			this.pageId = option.id;
			this.Token = server.net.GetSessionToken();
			this.getDetails(option.id);
 			this.GetImage();
 			this.GetReply();
			
		},
		onShow() {
			this.GetImage();
			this.GetReply();
		},
		methods: {
			getDetails(id) {
				let url = server.net.setUrl('api/SecurityInspectApi/GetList');
				console.log(id)
				let ajaxdata = JSON.stringify({
					ID: id
				})
				uni.request({
					url: url,
					method: 'POST',
					dataType: 'json',
					header: {
						Token: this.Token,
						"content-type": "application/json"
					},
					data: ajaxdata,
					success: res => {
						server.net.showWrongTipsEx(res);
						let result = res.data;
						let jsondata = result.data;
						console.log(res);
						if (result.status == 1) {
							//遍历JSON数组，获取数据对象
							jsondata.forEach((e) => {
								this.Title = e.Title;
								this.Company = e.Company;
								this.CompanyName = e.CompanyName; 
								this.Project = e.ProjectName; 
								this.Number = e.Number;
								this.Floor = e.Floor;
								this.Building = e.Building;
								this.SubcontractingUnit = e.SubcontractingUnit;
								this.SubcontractingUnitName = e.SubcontractingUnitName;
								this.SafetyDanger = e.SaftyDanger;
								this.DangerDes = e.DangerDes;
								this.RectPeople = e.RectPeopleName
								this.Rectification = e.Rectification;
								this.AddPeople = e.AddPeopleName;
								this.ExpirDate = e.ExpireDate.substring(0, 10);
								this.CheckDate = e.CheckDate.substring(0, 10);
								this.ReCheckDate = e.ReCheckDate.substring(0, 10);
								this.CrashLevel = e.CrashLevelName;
								this.State = e.StateName;
								this.ReState = e.State;
								this.RefuseReason = e.RefuseReason;
								switch (e.State) {
									case 0:
										this.isApproval = true;
										this.isRejected = true;
										break;
									case 1:
										this.isReply = true;
										break
									case 2:
										this.JudgeAc();
										break
								}
							});
						} else {
							console.log(result.message);
							uni.showToast({
								title: result.message
							})
						}

					},
					fail: (code) => {
						console.log(code)
						uni.showToast({
							title: '数据获取失败，请检查您的网络环境'
						});
					},

				});
			},
			JudgeAc(){
				let url = server.net.setUrl('api/QualityApi/JudgeAc?proid='+this.pageId);
				uni.request({
					url: url,
					method: 'POST', 
					header: {
						token: this.Token,
					},
					success: res => {
						server.net.showWrongTipsEx(res);
						if(res.data=='9'){
							this.isRecheck = true;
							this.isFinished = true;
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			Reply(type) { //安全隐患回复
			
				let SubType = 0;
				
				switch(type){
					case 1:
						SubType = 1;
						break;
					case 2:
						SubType = 2;
						break;
					break;
						default:
					break;
				}
			
				uni.navigateTo({
					url: "Repley?Type=1" + "&SubType=" +SubType+"&id="+this.pageId
				})
					
			},
			Review(state) {
				console.log(state);
				console.log(this.rejuectContent)
				let url = server.net.setUrl('api/SecurityInspectApi/Review');
				let ajaxdata = {
					id: this.pageId,
					state: state+'',
					content: this.rejuectContent
				}
				console.log(ajaxdata);
				uni.request({
					url: url,
					method: 'POST',
					data: JSON.stringify(ajaxdata),
					header: {
						token: this.Token
						// "content-type": "application/x-www-form-urlencoded"
					},
					success: res => {
						server.net.showWrongTipsEx(res);
						let result = res.data;
						uni.showToast({
							title: result.message
						});
						setTimeout(function() {
							uni.navigateBack({
								delta: 1
							})
						}, 2000)
					},
					fail: () => {},
					complete: () => {}
				});
			},previewImage: function (e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			GetImage:function(e){
				//获取单据图片
				console.log(this.pageId)
				let url = server.net.setUrl('api/FileApi/GetFileTable?'+'foreignId='+this.pageId+'&type=1');
				uni.request({
					url: url,
					method: 'POST',
					header: {
						token: this.Token,
					},
					success: res => {
						server.net.showWrongTipsEx(res);
						let result = res.data;
						let jsondata = result.data;
						let tempImage = [];
						for(let im of jsondata){
							let imsrc = server.net.setUrl(im.FileSrc);
							tempImage.push(imsrc)
						}
						this.imageList = tempImage;
					},
					fail: () => {},
					complete: () => {}
				});
			},
			GetReply:function(e){
				//获取回复列表   安全中复查为2  回复为1
				console.log(this.pageId)
				let url = server.net.setUrl('api/SecurityInspectApi/GetRepleyList');
				let data={
					qid:this.pageId
				}
				uni.request({
					url: url,
					method: 'POST',
					header: {
						token: this.Token,
					},
					data:JSON.stringify(data),
					success: res => {
						server.net.showWrongTipsEx(res);
						let result = res.data;
						let jsondata = result.data;
						console.log(jsondata);
						for(let it of jsondata){
							for(let im of it.FileImage){
								im.FileSrc = server.net.setUrl(im.FileSrc);
							}
						}
						this.repleyList = jsondata;
					},
					fail: () => {},
					complete: () => {}
				});
			}
			
			
			
		}
	}



